body {
  background-color: black;
  color:azure;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 40px;
  gap: 20px;
  margin-bottom: 80px;
  &_logo {
    width: 20vw;
    min-width: 280px;
  }
}

.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 60px;
  &_item {
    list-style: none;
    background-color: #7fcbd7;
    text-align: center;
    font-size: 20px;
  }
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  // gap: 20px;
  &_input {
//TO BE CENTERED
    border: 2px solid #97ce4c;
    width: 20vw;
    min-width: 280px;
    background-color: black;
    color: azure;
  }
  &_item {
    margin-top: 20px;
    width: 75%;
    display: flex;
    gap: 30px;
    justify-content: center;
    padding: 20px 0px 20px 0px;
    list-style: none;
    background-color: #7fcbd7;
    text-align: center;
    font-size: 20px;
  }
  &_return {
    font-size: 18px;
  }
}

.link {
  color: azure;
  outline: none;
}

.img {
  margin-top: 20px;
  width: 400px;
}